$fsize:18px;
$pink:#FD466A;
$fsmobile:16px;
$bp:1290px;
body,
html {
    margin: 0 auto;
    padding: 0 auto;
    overflow-y: auto;
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
    height: 100%;
    min-height: inherit;
    width: 100%;
    max-width: 100%;

    @media only screen and (max-width: $bp){
        font-size: $fsmobile;
    }
    font-size: $fsize;
    letter-spacing: 0.5px;
    //background: linear-gradient(135deg, #ff5f6d 20%, #ffc371 80%);
    color: #fff;
    text-align: center;
    font-family: 'Roboto Mono', monospace;

    p{
        margin: 0;
        span{
            color: $pink;
            text-transform:capitalize;

        }
    }

    .left{
        background: url(../imgs/bg.svg) no-repeat ;
        background-size: cover;
        height:100vh;
        max-width: 40%;
        width: 100%;
        float: left;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        min-height: 100vh;
        img{
            max-width: 466px;width: 100%
        }

        @media only screen and (max-width: $bp){
            float: none;max-width: 100%;max-height:350px;height:auto;min-height: auto;padding:30px 0;
          
            img{
                max-width: 250px;
            }
        }


    }

    .right{
        float: left;
        color: #717171;
        margin: 0 auto;
        max-width: 60%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        min-height: 100vh;
        position: relative;
        @media only screen and (max-width: $bp){
            float: none;max-width: 100%;
            padding: 20px 0px; min-height: 100%;height:100%;display: block;
        }

        .Copyright{
        position: absolute;
        bottom: 10px;
        left: 0;
        right: 0;
            p{
                font-size: 0.7rem
            }
        }


        .res{
            p{
                &:first-child{
                    font-weight: bold;
                    color:$pink;
                    font-size: $fsize;
                }
            }
        }
        h1{
           // color: #717171;
            font-size: 4rem;    margin: 0;
            @media only screen and (max-width: $bp){
                font-size: 2rem; 
            }
        }

        .words{
            margin: 40px 0px;
            h2{
                // color: #717171;
                 font-size: 5rem;    margin: 0;
                 color: $pink   ;    line-height: 5rem;
                 @media only screen and (max-width: $bp){
                    font-size: 2rem; line-height: 5rem;
                }
             }
             
        }
      
        span{
            
                font-size: 1rem;
                font-weight: 600;
                em{color: $pink

                }
            
        }
        input{
            box-shadow: 0px 5px 6px rgba(0, 0, 0, 0.16);
            border-radius: 40px;
            border: 1;
            border: 1px solid rgba(0, 0, 0, 0.16);
            outline: 0;
            width: 350px;
            height: 53px;
            line-height: 77px;
            font-size: 24px;
            padding: 0 10px;
            margin: 10px 0px;text-align: center
        }


        .checkboxme{
            input[type=checkbox]{
                height: 0;
                width: 0;
                visibility: hidden;
            }
            
            label {
                cursor: pointer;
                text-indent: -9999px;
                width: 60px;
                height: 30px;
                background: grey;
                display: block;
                border-radius: 50px;
                position: relative;    margin: 0 auto;
            }
            
            label:after {
                content: '';
                position: absolute;
                top: 5px;
                left: 5px;
                width: 20px;
                height: 20px;
                background: #fff;
                border-radius: 90px;
                transition: 0.3s;
            }
            
            input:checked + label {
                background: #bada55;
            }
            
            input:checked + label:after {
                left: calc(100% - 5px);
                transform: translateX(-100%);
            }
            
            label:active:after {
                width: 130px;
            }
            
        }

        &::after{
            clear: both;
        }
        
    }
}